import React from 'react';
const neatCsv = require('neat-csv');
const fs = require('fs')


async function exportTable(){
    fs.readFile('./Sentiment_Scores.csv', async (err, data) => {
      if (err) {
        console.error(err)
        return
      }
      return(await neatCsv(data))
    })
}

function Data(){
    return(
        <div className="data" >
        <h1>Data</h1>
        </div>

    );
}

export default Data;
